:root {
  --background: #ffffff;
  --text-primary: #000;
  --text-secondary: #8e05c2;
  --accent: #00ff37;
  --toggle: rgb(0, 0, 0);
}

[data-theme="dark"] {
  --background: #000;
  --text-primary: #fff;
  --text-secondary: #8e05c2;
  --accent: #00ff62;
  --toggle: rgb(255, 238, 0);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.app {
  background-color: var(--background);
  color: var(--text-primary);
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 0.8rem;
  transition: all 2.5s;
}

.text {
  font-size: 3.5rem;
  font-weight: bold;
  text-align: center;
}

.theme-toggle {
  position: fixed;
  top: 0;
  right: 0;
  padding: 10px;
  background-color: transparent;
  cursor: pointer;
}

.fa-sun {
  color: var(--toggle);
}
